<template>
  <div class="myMessage">
    <el-container>
      <el-aside width="200px">
        <div class="message-group">
          <remind v-for="(item,index) in remindList" :key="index" :data="item" :index="index" @messageCheck="messageCheck" :isChecked="checkedIndex===index"></remind>
        </div>
      </el-aside>
      <el-container>
        <div class="detail" v-if="checkedIndex!==''">
          <div v-if="remindList[checkedIndex].messageType==='2'" class="proving">
            <p>ID:{{remindList[checkedIndex].fromId}}向您发来好友验证:</p>
            <p class="question">您的问题:{{remindList[checkedIndex].remark}}</p>
            <p class="answer">对方回答:{{remindList[checkedIndex].message}}</p>
            <p class="time">日期:{{remindList[checkedIndex].time}}</p>
            <el-button type="primary" @click="addFriend">同意</el-button>
            <el-button type="danger" @click="rejectRequest">拒绝</el-button>
          </div>
          <div v-if="remindList[checkedIndex].messageType==='5'" class="proving">
            <p>ID:{{remindList[checkedIndex].fromId}}拒绝了你的好友邀请:</p>
            <p class="question">对方的问题:{{remindList[checkedIndex].remark}}</p>
            <p class="answer">您回答:{{remindList[checkedIndex].message}}</p>
            <p class="time">日期:{{remindList[checkedIndex].time}}</p>
          </div>
          <div v-if="remindList[checkedIndex].messageType==='6'" class="proving">
            <p>ID:{{remindList[checkedIndex].fromId}}邀你加入群:{{remindList[checkedIndex].remark.groupName}}</p>
            <p class="time">日期:{{remindList[checkedIndex].time}}</p>
            <el-button type="primary" @click="joinGroup">同意</el-button>
            <el-button type="danger" @click="rejectRequest">拒绝</el-button>
          </div>
        </div>
      </el-container>
    </el-container>
  </div>
</template>
<script>
  import remind from '../common/remind'
  import {timeFormat} from '../../util/common'
    export default {
        name:'myMessage',
        data: function () {
            return {
              remindList:[],
              checkedIndex:''
            };
        },
        components:{
          remind
        },
        created(){
          this.getAllRemind();
        },
        methods:{
          joinGroup(){
            this.$http.post('/joinGroup.do',{userId:this.$store.state.userId,groupId:this.remindList[this.checkedIndex].remark.groupId,id:this.remindList[this.checkedIndex].id}).then(res=>{
              if(res.data.success){
                this.$message({message:'成功加群',type:'success'});
              }
            })
          },
          messageCheck(index){
            this.checkedIndex=index;
          },
          rejectRequest(){
            this.$http.post('/rejectRequest.do',{toId:sessionStorage.getItem('userId'),fromId:this.remindList[this.checkedIndex].fromId,id:this.remindList[this.checkedIndex].id,messageType:this.remindList[this.checkedIndex].messageType}).then(res=>{
              if(res.data.success){
                this.$message({message:'已拒绝',type:'success'});
              }
            })
          },
          addFriend(){
            this.$http.post('/addFriend.do',{userId:sessionStorage.getItem('userId'),friendId:this.remindList[this.checkedIndex].fromId}).then(res=>{
              if(res.data.success){
                if(res.data.msg==='添加成功'){
                  this.$message({message:res.data.msg,type:'success'});
                  this.$http.post('/deleteRemind.do',{id:this.remindList[this.checkedIndex].id}).then(res=>{
                    if(res.data.success){

                    }
                  })
                }else{
                  this.$message.error(res.data.msg)
                }
              }
            })
          },
          getAllRemind(){
            this.$http.post('/getAllRemind.do',{userId:sessionStorage.getItem('userId')}).then(res=>{
              if(res.data.success){
                let temp=[];
                res.data.data.forEach(obj=>{
                  temp.push({
                    fromId:obj.UM_FromID,
                    message:obj.UM_Message,
                    time:timeFormat(parseInt(obj.UM_Time),'yyyy-MM-dd HH:mm'),
                    messageType:obj.UM_MessageType,
                    remark:obj.UM_MessageType==='6'?JSON.parse(obj.UM_Remark):obj.UM_Remark,
                    id:obj.UM_ID
                  })
                });
                this.remindList=temp;
                console.log(this.remindList)
              }
            })
          }
        }
    }
</script>
<style lang="less">
  .myMessage{
      height: 100%;
      >.el-container {
        height: 100%;
        .el-aside{
          background: rgb(84, 92, 100);
        }
        .detail{
          width: 100%;
          .proving{
            width: 300px;
            margin-left: 40%;
            margin-top: 10%;
            p:nth-of-type(1){
              margin-bottom: 10px;
            }
            p.question{
              margin-left: 30px;
            }
            p.answer{
              margin-left: 30px;
              margin-bottom: 30px;
            }
            p.time{
              text-align: right;
              font-size: 12px;
              margin-bottom: 5px;
            }
          }
        }
      }
  }
</style>



