<template>
  <div class="groupDetailDialog">
    <dialogs :dialogFlag.sync="dialogFlag" :openTitle="'ID:'+openData.groupId" :isDefaultBtn="false" @close="close" @open="open">
      <div slot="dialogContent">
        <div class="upload-wrap">
          <el-upload
            ref="uploadGroupIcon"
            :show-file-list="false"
            :auto-upload="false"
            class="avatar-uploader"
            :action='`/uploadGroupIcon.do?groupId=${openData.groupId}`'
            :file-list="fileList"
            :on-change="change"
            :on-success="handleAvatarSuccess">
            <img v-if="imageUrl" :src="imageUrl" class="avatar">
            <i v-else class="el-icon-plus avatar-uploader-icon"></i>
          </el-upload>
        </div>
        <div class="group-nick">
          <el-row>
            <el-col :span="17"><span>群名：</span></el-col>
            <el-col :span="7"><el-input v-model="params.groupName"></el-input></el-col>
          </el-row>
        </div>
        <div class="group-nick">
          <el-row>
            <el-col :span="17"><span>我的群片名：</span></el-col>
            <el-col :span="7"><el-input v-model="params.groupNick"></el-input></el-col>
          </el-row>
        </div>
        <!--<el-collapse @change="handleChange">-->
          <!--<el-collapse-item title="群成员" name="1">-->
            <!--<div class="member-wrap">-->
              <!--<div class="member" v-for="obj in openData.members">-->
                <!--<headPortrait :userId="obj.memberId"></headPortrait>-->
                <!--<p>{{obj.memberId}}</p>-->
              <!--</div>-->
            <!--</div>-->
          <!--</el-collapse-item>-->
        <!--</el-collapse>-->
        <el-popover
          popper-class="members-popover"
          ref="members"
          placement="right"
          trigger="click">
          <el-popover
            popper-class="friends-popover"
            ref="friendList"
            placement="left"
            @show="show"
            trigger="click">
            <div class="my-group">
              <el-menu
                @select="select"
                default-active="2"
                class="el-menu-vertical-demo"
                background-color="#545c64"
                text-color="#fff"
                active-text-color="#ffd04b">
                <el-submenu v-for="(item,index) in friendGroup" :key="index" :index="item.value+''">
                  <template slot="title"><span>{{item.name}}</span></template>
                  <el-menu-item v-for="(friend,index2) in item.content" :key="index2" :index="friend.friendId">
                    <div>
                      <headPortrait :userId="friend.friendId"></headPortrait>
                      {{friend.remark||friend.nickName||friend.friendId}}
                    </div>
                  </el-menu-item>
                </el-submenu>
              </el-menu>
            </div>
          </el-popover>
          <div v-popover:friendList><i class="el-icon-plus"></i><span>邀请新成员</span></div>
          <div class="table-box">
            <table>
              <tr v-for="obj in openData.members" :key="obj.memberId">
                <td><headPortrait :userId="obj.memberId"></headPortrait></td>
                <td>{{obj.groupNick||obj.memberId}}</td>
                <td><span>{{obj.authority===1?'群主':obj.authority===3?'群员':''}}</span></td>
              </tr>
            </table>
          </div>
        </el-popover>
        <div class="member-wrap" v-popover:members>
          <span>群成员</span>
          <i class="el-icon-arrow-right"></i>
        </div>
        <div class="group-intro">
          <el-row>
            <el-col :span="24">群介绍：</el-col>
            <el-col :span="23" :offset="1">
              <p>本群创建于{{new Date(parseInt(openData.createTime,10)).toLocaleString().split(' ')[0]}}</p>
              <el-input v-if="$store.state.userId===openData.adminId" v-model="params.groupIntro" type="textarea" placeholder="请输入群介绍"></el-input>
              <span v-else>{{openData.groupIntro}}</span>
            </el-col>
          </el-row>
        </div>
        <div class="button">
          <el-button type="primary" @click="saveGroupDetail">保存</el-button>
        </div>
      </div>
    </dialogs>
  </div>
</template>
<script>
  import dialogs from '../../common/dialogs'
  import headPortrait from './../../common/headPortrait'
  export default {
    name: 'groupDetailDialog',
    props: ['dialogFlag','openData'],
    data: function () {
      return {
        fileList:[],
        imageUrl:'',
        params:{
          groupNick:'',
          groupIntro:'',
          groupName:''
        },
        friendGroup:[]
      };
    },
    methods:{
      open(){
        this.fileList=[{name:'icon',url:`http://${this.$store.state.statisFileIp}${this.openData.icon}`}];
        this.imageUrl=`http://${this.$store.state.statisFileIp}${this.openData.icon}`;
        this.params.groupNick=this.openData.members.filter(obj=>obj.memberId===this.$store.state.userId)[0].groupNick||this.$store.state.nickName||this.$store.state.userId;
        this.params.groupIntro=this.openData.groupIntro;
        this.params.groupName=this.openData.groupName;
      },
      saveGroupDetail(){
        if(this.params.groupNick===''){
          this.$message.error('群片名不能为空');
          return
        }
        this.$http.post('/saveGroupDetail.do',{
          userId:this.$store.state.userId,
          groupId:this.openData.groupId,
          groupName:this.params.groupName,
          groupNick:this.params.groupNick,
          groupIntro:this.params.groupIntro,
        }).then(res=>{
          if(res.data.success){
            this.$message({message:'保存成功',type:'success'});
            this.$parent.getAllGroups();
            this.$emit('update:dialogFlag',false);
          }
        })
      },
      select(index,indexPath){
        let remark=this.openData;
        delete remark.members;
        this.$http.post('/inviteGroup.do',{toId:index,fromId:this.$store.state.userId,remark:remark}).then(res=>{
          if(res.data.success){
            this.$message({message:'已发送邀请',type:'success'})
          }
        })
      },
      handleChange(){

      },
      getFriend(){
        return new Promise(resolve => {
          this.$http.post('/getFriend.do',{userId:sessionStorage.getItem('userId')}).then(res=>{
            if(res.data.success){
              this.friendGroup.forEach((obj, index) => {
                this.friendGroup[index].content=[];
              });
              res.data.data.forEach((obj)=>{
                this.friendGroup.forEach((item,index)=>{
                  if(item.value==obj.F_FriendGroupsID){
                    this.friendGroup[index].content.push({
                      remark:obj.F_Name,
                      nickName:obj.U_NickName,
                      friendId:obj.F_FriendID
                    })
                  }
                })
              });
              resolve();
            }
          });
        })
      },
      getFriendGroup(){
        return new Promise(resolve => {
          // this.friendGroup=[];
          this.$http.post('/getFriendGroup.do',{userId:this.$store.state.userId}).then(res=>{
            if(res.data.success){
              let temp=[];
              res.data.data.forEach((obj,index)=>{
                temp.push({
                  name:obj.FG_Name,
                  value:obj.FG_ID,
                  content:[]
                })
              });
              this.friendGroup=temp;
              resolve();
            }
          })
        })
      },
      show(){
        (async ()=>{
          await this.getFriendGroup()
          await this.getFriend()
          // console.log(this.friendGroup)
        })()
      },
      change(file, fileList){
        let nameArr=file.name.split('.');
        let type=nameArr[nameArr.length-1].toLowerCase();
        const isLt2M = file.size / 1024 / 1024 < 2;
        const isTrueFormat = /jpeg|jpg|png|gif/i.test(type);
        !isLt2M&&this.$message.error('图片不能超过2Mb')
        !isTrueFormat&&this.$message.error('不识别此格式文件');
        if(isLt2M&&isTrueFormat){
          this.fileList=[fileList[fileList.length-1]];
          this.imageUrl = URL.createObjectURL(file.raw);
          this.$refs.uploadGroupIcon.submit();
        }else{
          this.fileList=[fileList[0]];
        }
      },
      handleAvatarSuccess(res, file) {
        if(res.success){
          this.$message({message:'群头像设置成功',type:'success'});
          let img=document.getElementsByClassName(`group${this.openData.groupId}`)[0].getElementsByTagName('img')[1];
          img.src=`http://${this.$store.state.statisFileIp}${res.data.replace(/\\/g, '/').split('/data')[1]}?time=${new Date().getTime()}`
        }
      },
      close(){
        this.$emit('update:dialogFlag',false)
      }
    },
    components: {
      dialogs,
      headPortrait
    }
  }
</script>
<style lang="less">
  .groupDetailDialog {
    .el-dialog {
      width: 30%;
      min-width: 300px;

      .upload-wrap{
        text-align: center;
        .avatar-uploader .el-upload {
          border: 1px dashed #d9d9d9;
          border-radius: 6px;
          cursor: pointer;
          position: relative;
          overflow: hidden;
        }
        .avatar-uploader .el-upload:hover {
          border-color: #409EFF;
        }
        .avatar-uploader-icon {
          font-size: 28px;
          color: #8c939d;
          width: 100px;
          height: 100px;
          line-height: 100px;
          text-align: center;
        }
        .avatar {
          width: 100px;
          height: 100px;
          display: block;
        }
      }
      .member-wrap{
        height: 40px;
        position: relative;
        span{
          line-height: 40px;
          margin-right: calc(~'100% - 70px');
        }
        /*.headPortrait{*/
          /*width: 30px;*/
          /*height: 30px;*/
        /*}*/
      }
      .group-intro{
        textarea{
          resize: none;
        }
      }
      .group-nick{
        span{
          line-height: 30px;
        }
        .el-input{
          .el-input__inner{
            text-align: right;
            height: 30px;
            border-radius: 0;
            border-right: none;
            border-left: none;
            border-top: none;
          }
        }
      }
      .button{
        text-align: center;
        margin-top: 15px;
      }
    }
  }
  .members-popover{
    /*width: auto !important;*/
    .table-box{
      max-height: 300px;
      overflow: auto;
      table{
        width: 200px;
        tr{
          .headPortrait{
            width: 30px;
            height: 30px;
          }
        }
      }
    }
   }
  .friends-popover{
    /*width: auto !important;*/
    .my-group{
      width: 180px;
      max-height: 300px;
      overflow: auto;
      .el-menu-item{
        min-width: auto;
        .headPortrait{
          display: inline-block;
          width: 30px;
          height: 30px;
          border-radius: 15px;
        }
      }
    }
  }
</style>



